<script>

	import moment from 'moment';
	// credit to https://github.com/nestjs/nest/tree/master/sample/02-gateways for getting the client & server socket.io stuff setup correctly
	import WSStuff from './stuff/WSStuff';
	import SimpleWeb from './stuff/Fetchy';

	// view components
	import Header from './components/Header.svelte';
	import Messages from './components/Messages.svelte';
	import Controls from './components/Controls.svelte';

	// default export bug: https://github.com/UnwrittenFun/svelte-atom/issues/2
	export let nestUrl;

	let messages = [];
	let timeFormat = "HH:mm:ss";

	// non-socket networking
	let webf = new SimpleWeb(nestUrl);
	// initial fetch of messages
	(async () => {
		let ms = await webf.getMessages();
		if ("rows" in ms) {
			messages = ms.rows
				.map(row => ({
					time: moment(row.timestamp).format(timeFormat),
					user: row.session,
					id: row.id,
					message: row.text
				}))
				.sort((m1, m2) => {
					// get times in milliseconds for comparison
					const [time1, time2] = [m1, m2].map(msg => new Date(msg.time).valueOf());
					return time2 > time1 ? 1 : time2 < time1 ? -1 : 0;
				});
		} else if ("error" in ms) {
			console.log(ms);
			messages = [{
				time: "just now",
				user: "dev",
				id: 0,
				message: "Something has gone terribly wrong. Check the console!"
			}];
		}
	})();

	// called from WSStuff to add messages
	function addNewMessage (msg) {
		msg.time = moment(msg.time).format(timeFormat);
		messages = messages.concat(msg);
	}

	function removeMessage (id) {
		messages = messages.filter(msg => msg.id !== id);
	}

	// socket related
	let wsInfo = new WSStuff(nestUrl, { addNewMessage, removeMessage });
	let connection = wsInfo.init();
	
	let userId;
	let ready = false;
	connection.then(async () => {
		userId = await wsInfo.getId();
		ready = true;
	});


	// handlers for events bubbled up from other components
	function handleSend (event) {
		wsInfo.sendMessage(event.detail.message);
	}

	function handleHide (event) {
		wsInfo.removeMessage(event.detail.mid);
	}

</script>

<!-- glorious svelte fragment -->
<div>
	<Header bind:userId={userId} 
		bind:connection={connection} />
	<Messages bind:messageList={messages} 
		bind:userId={userId} 
		on:hide={handleHide} />
	<Controls bind:ready={ready} 
		on:sendMessage={handleSend} />
</div>
