
<script>
    // disable ui until connection is open
    export let ready;

    import { createEventDispatcher } from 'svelte';
	const dispatch = createEventDispatcher();

    let outgoingMessage = "";
    async function sendMessage () {
		// ignore empty string submission
        if (!outgoingMessage) { return; }
        // dispatch will send the message up to the parent component to send the message
        // to the server
        dispatch("sendMessage", { message: outgoingMessage });
        outgoingMessage = "";
    }

    function handleKeyPress(event) {
		// enter key
		if (event.which === 13) {
			sendMessage();
		}
	}
</script>

<style type="text/scss">
    .controls {
        padding: 8px;
        border: 1px solid black;
        border-top: none;

        input, button {
            margin: 0;
        }
    }
</style>

<div class="controls">
    {#if ready}
    <div>
        <input type="text" maxlength="512" bind:value={outgoingMessage} on:keypress={handleKeyPress} />
        <button on:click={sendMessage}>send</button>
    </div>
    {/if}
</div>
