<script>

export let messageList = [];
export let userId = "";

import { tick } from 'svelte';
import { createEventDispatcher } from 'svelte';
const dispatch = createEventDispatcher();

// bound in template to allow node to be scrolled
let messageListNode;

function removeMessage(message) {
    dispatch("hide", {mid: message.id});
}

// messageListNode moved here to avoid the observer magic
function scrollContainer() {
    messageListNode.scrollTop = messageListNode.scrollHeight;
}

// svelte syntax for observe/compute
$: {
    // tell svelte to run this code every time the message list is updated
    messageList;

    // scroll the chat down after the message list changes
    tick().then(scrollContainer);
}

</script>

<style type="text/scss">

.messages {
    padding: 8px;
    border: 1px solid black;
}

.messages ul {
    padding: 0;
    margin: 0;
    height: 400px;
    overflow: auto;
}

ul.list {
    list-style: none;

    li {
        color: gray;
        .time { color: #76a4b3; }

        &.other {
            color: black;
            .time { color: blue; }
        }
    }
}

.delete {
    color: red;
    cursor: pointer;
}

</style>

<div class="messages">
    <ul class="list" bind:this={messageListNode} >
        {#each messageList as message}
            <li class={message.user === userId ? "" : "other"}>
                {#if message.user === userId}
                <span class="delete" title="Remove Message" on:click={() => removeMessage(message)}>[x]</span>
                {/if}
                <span class="time">
                    {message.user}@{message.time}
                </span>
                : {message.message}
            </li>
        {/each}
    </ul>
</div>
