/**
 * WSStuff (web socket stuff) handles websocket based network communications
 */

import io from 'socket.io-client';

export default class WSStuff {
    constructor (url, ops) {
        // passed from main.js props
        this.socketUrl = url;
        // pipes that allow this vanilla js file to run code from the svelte component
        this.ops = ops;
    }

    /**
     * handle some socket.io boilerplate and resolve when the connection is established
     */
    async init () {
        this.ws = io(this.socketUrl);
        return new Promise((resolve, reject) => {
            this.ws.on("connect", () => {
                this.handleConnect();
                resolve();
            });
            this.ws.on("events", d => this.handleEvents(d, this.ops));
            this.ws.on("disconnect", this.handleDisconnect);
            this.ws.on("exception", data => {
                this.handleException(data);
                reject();
            });
        });
    }

    /**
     * ask the server who we are, id used for enabling message hiding for your own messages
     */
    getId () {
        return new Promise((resolve, reject) => {
            this.ws.emit("identify", {}, response => {
                if (typeof response === "string") {
                    resolve(response.substring(0, 12));
                } else {
                    reject(response);
                }
            });
        });
    }

    sendMessage (msg) {
        this.ws.emit("events", { message: msg });
    }

    removeMessage (mid) {
        this.ws.emit("events", { remove: mid });
    }

    async handleConnect () {
        console.log("connected");
    }

    async handleDisconnect () {
        console.log("disconnected");
    }

    async handleEvents (data, ops) {
        console.log("events", data);
        const {addMessage, removeMessage} = data;
        if (addMessage) {
            ops.addNewMessage(addMessage);
        } else if (removeMessage) {
            ops.removeMessage(removeMessage);
        }
    }

    async handleException (data) {
        console.log("exception", data);
    }
};
