<script>

    // only vars in this component are passed in
    export let userId = "";
    export let connection;

</script>

<style>

    h4 { 
        margin: 0;
        padding: 8px;
        border: 1px solid black;
        border-bottom: none;
    }

    h4 .name {
        color: #76a4b3;
    }
</style>

<!-- explicit promise handling for templates! -->
<h4>
    Nest-Chat:
    {#await connection}
    connecting...
    {:then resolved}
    connected as <span class="name">{userId}</span>
    {:catch exception}
    connection failed
    {/await}
</h4>
